import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
export default new Router({
  routes: [
    // 采用异步调用组件，在访问‘/’时，才能进入以下路径，同时可以避免同时启动多个路径
    {
      path: '/',
      component: resolve => require(['../pages/Login.vue'], resolve)
    }

  ]
})



